import * as React from "react";
import * as ReactDOM from "react-dom";

import { Workbench } from "@ilabs/ui";
import { ZoningWorkbench } from "./components/ZoningWorkbench";
import { IZoningMetadata } from "./components/ZoningWorkbench/types";
import * as Constants from "./constants";
import registerServiceWorker from "./registerServiceWorker";

import "bootstrap/dist/css/bootstrap.min.css";

import "@ilabs/ui/styles/App.scss";
import "@ilabs/ui/styles/Sidebar.scss";

ReactDOM.render(
  <Workbench<IZoningMetadata>
    appTitle={Constants.APP_TITLE}
    basename={process.env.PUBLIC_URL || "/"}
    blockingTaskKind={Constants.TASK_KIND}
    roleWhitelist={[Constants.META_ROLE]}
  >
    {opts => <ZoningWorkbench workbench={opts} />}
  </Workbench>,
  document.getElementById("root") as HTMLElement,
);
registerServiceWorker();



