import React, { useMemo, useState } from "react";

import { IRenderJobOptions } from "@ilabs/ui";
import * as Constants from "../../constants";
import { Zoning } from "../Zoning";
import { Size } from "../Zoning/Renderer/types";
import { IZoningMetadata } from "./types";

interface IProps {
  workbench: IRenderJobOptions<IZoningMetadata>;
}

interface IState {
  error: Error | null;
  saving: boolean;
}

export const ZoningWorkbench = ({ workbench }: IProps) => {
  const {
    actions: { fetchContent },
    state: { contents, job, user },
  } = workbench;
  const [{ error, saving }, setState] = useState<IState>({
    error: null,
    saving: false,
  });
  const [
    pageConfidences,
    pageDimensions,
    highResImageURIs,
    lowResImageURIs,
    pageZoneIds,
    pageZoneURIs,
  ] = useMemo(() => {
    if (!job || !user || !contents || !Array.isArray(contents.pages)) {
      return [[], [], [], [], [], []];
    }
    let highResImages = job.contents
      .filter(c => c.role === Constants.HI_RES_IMAGE_ROLE)
      .map(c => `${c.uri}?token=${user.api_keys.live || ""}`);
    if (highResImages.length < 1) {
      highResImages = job.contents
        .filter(c => c.role === Constants.LO_RES_IMAGE_ROLE)
        .map(c => `${c.uri}?token=${user.api_keys.live || ""}`);
    }
    return [
      job.contents
        .filter(c => c.role === Constants.LO_RES_IMAGE_ROLE)
        .map((c, i) => Number.parseFloat(job.metadata[`page_confidence_${i}`])),
      contents.pages
        .map(
          (p): Size => {
            if (typeof p.images !== "object" || p.images === null || Array.isArray(p.images)) {
              return { height: 0, width: 0 };
            }
            const { height, width } = p.images;
            return { height, width };
          },
        )
        .filter(s => !isNaN(s.height) && s.height > 0 && !isNaN(s.width) && s.width > 0),
      highResImages,
      job.contents
        .filter(c => c.role === Constants.LO_RES_IMAGE_ROLE)
        .map(c => `${c.uri}?token=${user.api_keys.live || ""}`),
      job.contents.filter(c => c.role === Constants.ZONES_IN_ROLE).map(c => c.id),
      job.contents
        .filter(c => c.role === Constants.ZONES_IN_ROLE)
        .map(c => `${c.uri}?token=${user.api_keys.live || ""}`),
    ];
  }, [job, user]);
  if (error) {
    return <div>{error.message}</div>;
  }
  if (!job) {
    return null;
  }
  return (
    <Zoning
      fetchJobContent={fetchContent}
      highConfidenceThreshold={Number.parseFloat(job.metadata.high_confidence_threshold) || 1.0}
      highResImageURIs={highResImageURIs}
      jobId={job.id}
      lowResImageURIs={lowResImageURIs}
      onSave={async (editedPages, modifiedPages) => {
        if (!job) {
          return;
        }
        setState({
          error,
          saving: true,
        });
        const jobOutput = job.contents.filter(c => c.role === Constants.ZONES_OUT_ROLE);
        await Promise.all(
          editedPages.map((p, i) => {
            if (modifiedPages.indexOf(i) < 0) {
              // we do not need to save pages that have size > 0 and were not modified
              return Promise.resolve();
            }
            return workbench.actions.saveContent(
              jobOutput[i].id,
              JSON.stringify({
                ...p,
                predictions: p.predictions.map(prediction => {
                  if (prediction.class.length > 0) {
                    return prediction;
                  }
                  return {
                    ...prediction,
                    class: "text",
                  };
                }),
              }),
            );
          }),
        );
        return workbench.actions.checkinJobSuccess();
      }}
      pageConfidences={pageConfidences}
      pageDimensions={pageDimensions}
      pageZoneIds={pageZoneIds}
      pageZoneURIs={pageZoneURIs}
      saving={saving}
      startingPage={0}
    />
  );
};



